export default {
	data() {
		return {
			imgArr: []
		}
	},
	methods: {
		/**
		 * 上传图片
		 */
		clickHandle() {
			uni.chooseImage({
				count: 5,
				success: (res) => {
					console.log(res)
					this.imgArr = res.tempFilePaths
				}
			})
		},
		imgClick(item) {
			uni.previewImage({
				current: item,
				urls: this.imgArr
			})
		}
	}
}
